@import url(https://fonts.googleapis.com/css?family=Raleway&display=swap);


.colorTA {
  color: #01579b;
  text-shadow: 0.1px 0.1px 1px;
}

.colored {
  color: #FF4500;
  text-shadow: 0.1px 0.1px 1px;
}

.word{
  transition: .5s;
}

.line{
  list-style: none;
  margin: 0px;
  padding: 0px;
  position: relative;
  line-height: 1.3;
}


#ThinkAloud .thoughts{
  color: #9e9e9e;
  width: 350px;
}


#ThinkAloud .thoughts:after{
  content: ' ';
  display: block;
  height: 25px; /* 5px + 5px = 10px */
}

#ThinkAloud .thoughts li{
  list-style: none;
  font-size: 12px;
  padding: 6px 12px;
  -webkit-text-size-adjust: 100%;
     -moz-text-size-adjust: 100%;
          text-size-adjust: 100%;
  transition: .5s;
  -webkit-hyphens: auto;
      -ms-hyphens: auto;
          hyphens: auto;
  font-family: Verdana, Geneva, Tahoma, Arial, sans-serif;
}

#ThinkAloud .thoughts .hovered{
  color: #FF4500;
}

#ThinkAloud .thoughts li.active{
  font-size: 14px;
  background-color: #ecf6fd;
  color: black;
  margin: 12px 0px;
  cursor: pointer;
  position: relative;
  border: 1px solid lightgrey;
}

#ThinkAloud .thoughts li.active:hover{
  border-color: #40a9ff;
}

#ThinkAloud .thoughts li.active:hover .next{
  color: #40a9ff;
}

#ThinkAloud .thoughts .next{
  position: absolute;
  right:10px;
  bottom: 5px;
  color: grey;
}
