#ThinkAloud .content{
  display: flex;
}

#ThinkAloud .menu li{
  display: inline;
  padding-right: 25px;
}

#ThinkAloud .menu li:hover{
  color: #40a9ff;
  cursor: pointer;
  transition: .5s;
}

#ThinkAloud .menu{
  padding: 0;
  margin: 0;
}

#ThinkAloud .menu .ant-progress-text {
  position: absolute;
}

#ThinkAloud .line{
    margin: 0px;
    padding: 0px;
    position: relative;
    line-height: 1.3;
    color: #9e9e9e;
  }

#ThinkAloud .poem{
  padding: 0px;
  margin: 16px 0px;
}

#ThinkAloud .title h2{
  color: #9e9e9e;
  font-size: 19px;
  margin: 0;
}

#ThinkAloud .poem li{
  list-style: none;
  font-size: 17px;
  -webkit-text-size-adjust: 100%;
     -moz-text-size-adjust: 100%;
          text-size-adjust: 100%;
  margin-bottom: 6px;
}

#ThinkAloud {
  font-family: 'Raleway';
  font-weight: 500;
}

.tooltipTA{
  max-width: 275px;
}

.tooltipTA .ant-popover-arrow{
  border-color: #ecf6fd !important;
  box-shadow: none !important;
  -webkit-box-shadow: none !important;
  border-width: 5px;
  left: 5.5px;
}

.tooltipTA .ant-popover-inner-content{
  background-color: #ecf6fd;
  padding: 1px;
  cursor: pointer;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.tooltipTA .ant-popover-inner{
  box-shadow: none;
  -webkit-box-shadow: none;
}

.thinkaloud-tooltip{
  background: #ecf6fd;
  padding: 8px 16px;
  font-size: 12px;
  font-family: Verdana, Geneva, Tahoma, Arial, sans-serif;
  -webkit-hyphens: auto;
      -ms-hyphens: auto;
